body {
  padding: 20px;
}
.data-container {
  max-width:400px;
  max-height: 500px;
  padding-right: 10px;
  overflow-y: auto;
}
.data-container ul {
    list-style: none;
    margin:0;
    padding:0;
}
.data-container ul li {
    border: 1px solid #ccc;
    padding: 10px 20px;
    margin-bottom: 10px;
    color: #333;
    font-size: 14px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
}